
import { element } from 'protractor';
import { Icontacts } from './../contacts/contactsIF';
import { Component,OnInit,Output,EventEmitter,HostListener } from '@angular/core';
import { contactsService } from './../contacts/contacts.service';
import { observable, Observer, Observable,interval } from 'rxjs';


@Component({
    selector: 'app-tabs',
    templateUrl: 'app-tabs.component.html',
    styleUrls: ['app-tabs.component.css'],
    providers:[contactsService],
})
export class AppTabsComponent {
loadChatContacts:boolean=true;
viewFilter:boolean=false;   
contacts:Icontacts[];
clickedContact:Icontacts[];
contactClicked:boolean=false;
sendNotify:Observable<string>
public innerWidth: any;
ngOnInit() {
    this.innerWidth = window.innerWidth;
    this.onScreenResize()

}
@HostListener('window:resize', ['$event'])
onResize(event) {
  this.onScreenResize()
}
onScreenResize(){
  this.innerWidth = window.innerWidth;
  if(this.innerWidth<700){
    document.getElementById("contListContain").className += " fullWidth ";
    document.getElementById("chatBoxCont").className += " fullWidth ";
    if(this.contactClicked){
        document.getElementById("contListContain").className += "hide";
    }else{
        document.getElementById("chatBoxCont").className += " hide";

    }
  }else{
   document.getElementById("contListContain").className = "leftSideCont";
   document.getElementById("chatBoxCont").className = "chatBox";
  }
}
 constructor(private _contactsService:contactsService){
this.contacts= this._contactsService.getContacts();
this.viewChatContacts()
this.sendNewMsg().subscribe(
    next=>{},
)


 }
sendNewMsg(){
    this.sendNotify =new Observable(observer=>{
        setInterval(()=>{ 
           this._contactsService.reciveNewMsg();
           this.updateContacts()
           if(this.loadChatContacts){
               this.viewChatContacts()
           }

         },30000);
    })
    return this.sendNotify
}
trackContacts(index:number,contact:any):string{
return contact.id;

}
viewChatContacts(){
    this.loadChatContacts=true;
    this.viewFilter=false;
    this.contacts= this.contacts.filter((contact: any) => contact.hasMsg === true);
}

updateContacts(){
    this.contacts= this._contactsService.getContacts();
}

viewAllContacts(){
    this.loadChatContacts=false;
    this.viewFilter=false;
    this.updateContacts()
}
viewFilterTab(){
    this.loadChatContacts=false;
    this.viewFilter=true;
    this.updateContacts()   
}
    
filterContacts(inputValue){
    debugger
     if(inputValue!==''){
            this.contacts=this._contactsService.getContacts().filter((contact: any) => contact.name.toLowerCase().includes(inputValue.toLowerCase()));
 
     }else{
        this.updateContacts()
     }
}

changeClickedId(contactId){
    this._contactsService.getContactId(contactId);
 }


 getChatForContact(contactId,event){
     
    var contacList=document.getElementsByClassName("contact");
    [].forEach.call(contacList, function(el){
        el.classList.remove("active");
    })
    event.currentTarget.className+=" active";
     this.contactClicked=true;
    this.clickedContact=this.contacts.filter((contact: any) => contact.id === contactId);
    this.clickedContact[0].hasNotif=false;
    this._contactsService.readNewMsg(contactId);


    if(window.innerWidth<700){
        document.getElementById("chatBoxCont").classList.remove("hide");
        document.getElementById("contListContain").classList.add("hide");
      }
 }
}

