
import { Component,Input } from '@angular/core';
import { Icontacts } from './../contacts/contactsIF';
import { AppTabsComponent } from './../app-tabs/app-tabs.component';
import { contactsService } from './../contacts/contacts.service';
import { SOURCE } from '@angular/core/src/di/injector';

@Component({
    selector: 'chat-box',
    templateUrl: 'chat-box.component.html',
    styleUrls: ['chat-box.component.css'],
    providers:[contactsService],
})
export class ChatBoxComponent {
    contactId:number;
    contacts:Icontacts[];
    contactMessages:Array<object>
    contact:any;
    @Input() clickedContact:any;
    constructor(private _contactsService:contactsService){
        this.contacts= this._contactsService.getContacts();
        this.contactId= this._contactsService.passContactId();
        this.contact=this.contacts.filter((contact: any) => contact.id===this.contactId);
         }

         sendNewMsg(input,contctId,newMsg){

             var currentDate=this._contactsService.getCurrentTiem();
            input.reset();
        var newSendMsg={"text":newMsg+" to","type":"send","time":currentDate};
           this.clickedContact.hasMsg=true;
           this.clickedContact.messages.push(newSendMsg)
           this._contactsService.updateUserMsg(contctId,newSendMsg);
         }   
         
         backToChatList(){
            document.getElementById("contListContain").classList.remove("hide");
            document.getElementById("chatBoxCont").classList.add("hide");

         }       
         
         sendChatByEnter(input,contctId,newMsg){
             if(newMsg.length>0){
            this.sendNewMsg(input,contctId,newMsg);
             }
         }
}
